.btn {
  border: 2px solid white;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-family: "Lilita One", sans-serif;
  transition: all 500ms ease;
  display: block;
  margin: 30px auto 0 auto;
}

.btn:hover {
  background-color: white;
  color: black;
}
