.btn {
  border: 2px solid white;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-family: "Lilita One", sans-serif;
  transition: all 500ms ease;
}

.btn:hover {
  background-color: white;
  color: black;
}

.btn:disabled {
  background-color: orangered;
  border: 1px solid black;
  color: black;
}
